import React, {Component, Fragment} from 'react';
import {connect} from "react-redux";
import {Form, Button, Input, Radio, Icon, Upload} from "antd";
import * as actionCreators from '../store/actionCreators';

class EditReplyInfo extends Component {

  componentDidMount() {
    let query = this.props.history.location.query;
    query && this.props.initReplyInfo(query);
  }

  render() {
    const { replyInfo, content, uploadImg, handleReplyChange, handleContentChange, submitReply, cancel } = this.props;
    const formItemLayout = {
      labelCol: {
        sm: {span: 6}
      },
      wrapperCol: {
        sm: {span: 16, offset: 0}
      },
      colon: false
    };
    let textOrImgText;
    if(replyInfo.type === 0) {
      textOrImgText = <Form.Item label="详细信息">
        <Input type="textarea" value={content.description} placeholder="请输入详细信息" onChange={e => handleContentChange(e, "description")} />
      </Form.Item>
    } else {
      let UploadImg;
      if(content.picurl) {
        UploadImg = <img style={{width: "100px"}} src={content.picurl} alt="图片走丢啦~" />
      } else {
        UploadImg = <Button><Icon type="upload" /> 上传图片 </Button>
      }
      textOrImgText = <Fragment>
        <Form.Item label="图文标题">
          <Input value={content.title} placeholder="请输入图文标题" onChange={e => handleContentChange(e, "title")} />
        </Form.Item>
        <Form.Item label="图文链接">
          <Input value={content.url} placeholder="请输入图文链接" onChange={e => handleContentChange(e, "url")} />
        </Form.Item>
        <Form.Item label="图片">
          <Upload name="imageFile" action="/reply/upload" onChange={(info) => {uploadImg(content, info)}} showUploadList={false}>
            {UploadImg}
          </Upload>
        </Form.Item>
      </Fragment>
    }
    return (
      <div>
        <div className='container'>
          <div className="title-wrapper">
            <h2>编辑素材</h2>
          </div>
          <Form style={{width: 400}} {...formItemLayout}>
            <Form.Item label="类型">
              <Radio.Group value={replyInfo.type} onChange={e => handleReplyChange(e, "type")}>
                <Radio.Button value={0}>文本</Radio.Button>
                <Radio.Button value={1}>图文</Radio.Button>
              </Radio.Group>
            </Form.Item>
            {textOrImgText}
            <Form.Item label=" ">
              <Button type="primary" style={{marginRight: 10}} onClick={() => submitReply(content, replyInfo, this.props)}>提交</Button>
              <Button onClick={() => cancel(this.props)}>取消</Button>
            </Form.Item>
          </Form>
        </div>
      </div>
    )
  }

  componentWillUnmount() {
    this.props.unMountState();
  }
}

const mapState = state => ({
  replyInfo: state.source.group.replyInfo,
  content: state.source.group.content
});

const mapDispatch = dispatch => ({
  uploadImg(content, info) {
    if (info.file.status === 'done') {
      let picurl = `http://t.dmmup.com/uploads/${info.file.response.filename}`
      dispatch(actionCreators.handleContentChange(picurl, "picurl"))
    }
  },
  handleContentChange(e, name) {
    dispatch(actionCreators.handleContentChange(e.target.value, name))
  },
  handleReplyChange(e, name) {
    dispatch(actionCreators.handleReplyChange(e.target.value, name))
  },
  cancel(props) {
    props.history.goBack();
    dispatch(actionCreators.resetState())
  },
  submitReply(content, replyInfo, props) {
    if(props.history.location.query) {
      replyInfo.contents.splice(0, 1, content);
    } else {
      replyInfo.contents.push(content);
    }
    dispatch(actionCreators.submitReply(replyInfo, props))
  },
  initReplyInfo(record) {
    dispatch(actionCreators.initReplyInfo(record))
  },
  unMountState() {
    dispatch(actionCreators.resetState())
  }
});

export default connect(mapState, mapDispatch)(Form.create({name: 'editReplyInfo'})(EditReplyInfo));