import React, { Component } from 'react';
import { Button, Input, Form, Modal, message, Select } from 'antd';
import axios from 'axios';

const { Option } = Select;

const initState = {
  gonghaoList: [],
  qrCodeInfo: {
    name: "",
    content: "",
    code: null
  }
};

class EditQrCode extends Component {
  constructor(props) {
    super(props);
    this.cancelEdit = this.cancelEdit.bind(this);
    this.submitEdit = this.submitEdit.bind(this);
  }
  state = JSON.parse(JSON.stringify(initState));
  render() {
    const { gonghaoList, qrCodeInfo } = this.state;
    const { query } = this.props.history.location;
    return (
    	<div>
        <div className="container">
          <div className="title-wrapper">
            <h2>编辑二维码信息</h2>
            <div className="fr">
              <Button type="primary" style={{marginRight: 10}} onClick={this.submitEdit}>提交</Button>
              <Button onClick={this.cancelEdit}>取消</Button>
            </div>
          </div>
          <Form className="fl" style={{width: "40%"}} labelCol={{sm: 6}} wrapperCol={{sm: 18}}>
            <Form.Item label="二维码名称">
              <Input value={qrCodeInfo.name} placeholder="请输入二维码名称" onChange={e => this.changeInputValue(e.target.value, "name")} />
            </Form.Item>
            <Form.Item label="关注回复内容">
              <Input.TextArea value={qrCodeInfo.content} placeholder="请输入关注回复内容" onChange={e => this.changeInputValue(e.target.value, "content")} />
            </Form.Item>
            <Form.Item label="选择公号">
              <Select
                showSearch
                style={{ width: 200 }}
                placeholder="输入公号/选择公号"
                optionFilterProp="children"
                value={qrCodeInfo.code ? this.getGonghaoName(qrCodeInfo.code) : qrCodeInfo.gonghao}
                onChange={value => this.changeInputValue(value, "code")}
                filterOption={(input, option) =>
                  option.props.children.indexOf(input) >= 0
                }
                disabled={!!query}
              >
                {gonghaoList.map(item => <Option key={item._id} value={item.code}>{item.nick_name}</Option>)}
              </Select>
            </Form.Item>
          </Form>
        </div>
    	</div>
    )
  }

  componentDidMount() {
    const { query } = this.props.history.location;
    this.getGonghaoList();
    query && this.initState(query)
  }

  getGonghaoName(code) {
    if(code) {
      const { gonghaoList } = this.state;
      let result = gonghaoList.filter(item => item.code === parseInt(code));
      return result.length > 0 && result[0].name
    }
  }

  initState(query) {
    axios.get("/tag/get_name", { params: { tagId: query.tagId } })
      .then(res => {
        if(res.data.data) {
          const { name } = res.data.data;
          query.tagName = name;
          this.setState(state => ({qrCodeInfo: {...query,}}))
        }
      });
  }

  getGonghaoList() {
    axios.get("/qr_code/get_code").then(res => {
      const { codes: gonghaoList } = res.data;
      this.setState({gonghaoList})
    });
  }

  changeInputValue(value, name) {
    const { qrCodeInfo } = this.state;
    qrCodeInfo[name] = value;
    this.setState(state => ({qrCodeInfo: {...state.qrCodeInfo, ...qrCodeInfo}}))
  }

  submitEdit() {
    const { qrCodeInfo } = this.state;
    const { query } = this.props.history.location;
    let url = "/qr_code/create";
    if(query) {
      qrCodeInfo.id = qrCodeInfo._id;
      url = "/qr_code/update";
    }
    Modal.confirm({
      title: "确定提交吗？",
      onOk: () => {
        axios.post(url, {...qrCodeInfo})
          .then(res => {
            const { data } = res.data;
            if (!data) {
              message.error(query ? "修改失败" : "创建失败");
            } else {
              message.info(query ? "修改成功" : "创建成功");
              this.cancelEdit();
            }
          });
      }
    })
  }

  cancelEdit() {
    this.props.history.goBack();
    this.resetState();
  }

  resetState() {
    this.setState({...initState})
  }

  componentWillUnmount() {
    this.resetState();
  }
}

export default EditQrCode;