import axios from 'axios';
import * as constants from './constants';
import { Modal, message } from 'antd';

// 群发公号
const getData = () => {
  return new Promise((resolve, reject) => {
    axios.get("/message", {params: {sort: "_id"}})
      .then(res => {
        if (res.data.messages.length > 0) {
          let kefuMessageList = res.data.messages;
          const action = {
            type: constants.GET_KEFU_MESSAGE_LIST,
            kefuMessageList
          };
          resolve(action)
        } else {
          reject('error message: 没有数据')
        }
      })
  });
};

export const getKefuMessageList = () => {
  return async dispatch => {
    let action = await getData();
    dispatch(action);
  }
};

export const createKefuMessage = record => {
  return dispatch => {
    Modal.confirm({
      title: "确定新增一条客服消息吗？",
      onOk: () => {
        axios.post("/message/create", {...record})
          .then(res => {
            if (res.data.success) {
              let data = res.data.data
              message.info(record._id ? "复制成功" : res.data.success);
              const action = {
                type: constants.CREATE_KEFU_MESSAGE,
                data
              };
              dispatch(action);
            } else {
              message.info(res.data.err);
            }
          })
      }
    })
  }
};

export const deleteMessage = (id, index) => {
  return dispatch => {
    Modal.confirm({
      title: "确定删除这条客服消息吗？",
      onOk: () => {
        axios.get("/message/delete", { params: { id } })
          .then(res => {
            if (res.data.data.length === 0) {
              message.info("已删除最后一条数据");
              const action = {
                type: constants.DELETE_LAST_MESSAGE
              };
              dispatch(action);
            } else {
              message.info(res.data.success);
              const action = {
                type: constants.DELETE_MESSAGE,
                index
              };
              dispatch(action);
            }
          });
      }
    })
  }
};

export const sendKefuMessage = (id, tagId) => {
  return dispatch => {
    Modal.confirm({
      title: "确定发送消息吗？",
      onOk: () => {
        axios.get("/message/send", { params: { id, tagId, take_over: true } })
          .then(res => {
            message.info(res.data.success);
          });
      }
    })
  }
};

