import React, { Component } from 'react';
import { Editor } from '@tinymce/tinymce-react';

class Index extends Component {
  handleInputChange = e => {
    console.log(e.target.getContent())
  };

  render() {
    return (
    	<div className="container">
        <Editor
          apiKey="0716d4da8zojzntbxderqtt6hahy4pg7832a1gj024wxtzdp"
          init={{
            menubar: "edit",
            max_height: 750,
            plugins: [
              'image code lists emoticons advlist link charmap codesample fullscreen help hr insertdatetime nonbreaking preview searchreplace table'
            ],
            toolbar: 'formatselect | fontsizeselect | fontselect | bold italic underline strikethrough forecolor backcolor | alignleft aligncenter alignright alignjustify  | numlist bullist outdent indent | charmap nonbreaking hr | table | insertdatetime link image emoticons codesample | preview fullscreen code | searchreplace help',
            min_height:500,
            default_link_target: "_blank",
            fontsize_formats: "12px 14px 16px 18px 20px 22px 24px 30px 36px 40px 48px 50px",
          }}
          onChange={this.handleInputChange}
        />
    	</div>
    )
  }
}

export default Index;