import React, { Component } from 'react';
import { Button, Table, Modal, message } from 'antd';
import axios from 'axios';
import { Link } from "react-router-dom";

class QrCode extends Component {
  state = {
    qrCodeList: []
  };
  render() {
    const { qrCodeList } = this.state;
    const qrCodeHeader = [
      {
        key: "name",
        dataIndex: "name",
        title: "二维码名称",
        align: "center",
        width: 300
      },
      {
        key: "content",
        dataIndex: "content",
        title: "关注后回复内容",
        align: "center",
      },
      {
        key: "gonghao",
        dataIndex: "gonghao",
        title: "公号",
        align: "center",
        width: 300
      },
      {
        key: "qr_code_url",
        dataIndex: "qr_code_url",
        title: "二维码",
        align: "center",
        width: 200,
        render: (text, record) => <img style={{width: "100%"}} src={text} alt=""/>
      },
      {
        title: "Action",
        align: "center",
        width: 200,
        render: (text, record, index) => <div>
          <Button style={{marginRight: 10}} size="small" type="primary">
            <Link to={{pathname: "./update", query: record}}>修改</Link>
          </Button>
          <Button size="small" type="danger" onClick={() => this.deleteQrCode(record._id, index)}>删除</Button>
        </div>
      }
    ];
    return (
    	<div>
        <div className="container">
          <Button type="primary" style={{marginBottom: 20}}><Link to="./create">新增参数二维码</Link></Button>
          <Table rowKey={record => record._id} size="small" bordered columns={qrCodeHeader} dataSource={qrCodeList} />
        </div>
    	</div>
    )
  }

  componentDidMount() {
    this.getQrCodeList()
  }

  getQrCodeList() {
    axios.get("/qr_code/show").then(res => {
      let {codes, data} = res.data;
      for (var i = 0, codesLength = codes.length; i < codesLength; i++) {
        for (var j = 0, dataLength = data.length; j < dataLength; j++) {
          if (codes[i].code === parseInt(data[j].code)) {
            data[j].gonghao = codes[i].name;
          }
        }
      }
      this.setState({qrCodeList: data.reverse()});
    });
  }

  deleteQrCode(id, index) {
    const { qrCodeList } = this.state;
    Modal.confirm({
      title: "确定删除这条内容吗？",
      onOk: () => {
        axios.post("/qr_code/deleteOne", { id })
          .then(res => {
            message.info(res.data.success);
            qrCodeList.splice(index, 1);
            this.setState({qrCodeList})
          });
      }
    })
  }
}

export default QrCode;