import React, { Component } from 'react';
import { Link } from "react-router-dom";
import { Avatar, Popover, Icon, message } from 'antd';

import './index.scss';

class HeaderWrapper extends Component {

  state = {
    username: ""
  };

  componentDidMount() {
    let userInfo = localStorage.getItem("userInfo");
    if(userInfo) {
      userInfo = JSON.parse(userInfo);
      this.setState({username: userInfo.username})
    }
  }

  render() {
    return (
      <div className='header-wrapper'>
        <Link to="/">
          <img className="logo fl" src={require("../../../assets/images/logo.png")} alt=""/>
        </Link>
        <div className="user-info fr">
          <Avatar shape="square" size={25} icon="user" />
          <Popover content={<p style={{fontSize: 12}} className="cursor" onClick={logout}><Icon type="logout" />退出</p>}>
            <div className="user fr cursor">
              <p>{this.state.username},</p>
              <p>欢迎回来<Icon type="down" /></p>
            </div>
          </Popover>
        </div>
      </div>
    )
  }


}

const logout = () => {
  localStorage.setItem("userInfo", "");
  message.success("退出登录成功");
  setTimeout(() => {
    window.location.href = "/admin/login";
  }, 500)
};

export default HeaderWrapper;
