import * as constants from './constants';

const defaultState = {
  replyList: [],
  content: {
    picurl: "",
    title: "",
    url: "",
    description: ""
  },
  replyInfo: {
    type: 0,
    contents: [],
    description: ""
  }
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    case constants.GET_REPLY_LIST:
      newState.replyList = action.replyList;
      return newState;
    case constants.DELETE_REPLY:
      newState.replyList.splice(action.index, 1);
      return newState;
    case constants.HANDLE_CONTENT_CHANGE:
      newState.content[action.name] = action.value;
      return newState;
    case constants.HANDLE_REPLY_CHANGE:
      if(action.name === "type") {
        newState.content = defaultState.content;
        newState.replyInfo.contents = [];
      }
      newState.replyInfo[action.name] = action.value;
      return newState;
    case constants.INIT_REPLY_INFO:
      newState.replyInfo = {...action.record};
      newState.replyInfo.id = newState.replyInfo._id;
      newState.content = {...newState.replyInfo.contents[0]};
      return newState;
    case constants.COPY:
      newState.replyList.unshift({...action.data});
      return newState;
    case constants.RESET_STATE:
      newState = defaultState;
      return newState;
    default:
      return state;
  }
};