import axios from 'axios';
import * as constants from './constants';
import { Modal, message } from 'antd';


export const getReplyList = () => {
  return dispatch => {
    axios.get('/msg')
      .then(res => {
        if (res.data.data.length > 0) {
          let replyList = res.data.data.reverse();
          const action = {
            type: constants.GET_REPLY_LIST,
            replyList
          };
          dispatch(action);
        }
      })
  }
};

export const deleteReply = (id, index) => {
  return dispatch => {
    Modal.confirm({
      title: "提示",
      content: "确定删除吗？",
      onOk: () => {
        axios.get("/msg/del", {params: {id}})
          .then(res => {
            if(res.data.success) {
              const action = {
                type: constants.DELETE_REPLY,
                index
              };
              dispatch(action);
              message.success(res.data.success)
            }
          });
      }
    })
  }
};

export const handleContentChange = (value, name) => ({
  type: constants.HANDLE_CONTENT_CHANGE,
  name, value
});

export const handleReplyChange = (value, name) => ({
  type: constants.HANDLE_REPLY_CHANGE,
  name, value
});

export const resetState = () => ({
  type: constants.RESET_STATE
});

export const submitReply = (replyInfo, props) => {
  return dispatch => {
    Modal.confirm({
      title: "提示",
      content: "确定提交吗？",
      onOk: () => {
        axios.post(props.history.location.query ? "/msg/update" : "/msg/create", {...replyInfo})
          .then(res => {
            if(res.data.success) {
              resetState();
              message.success(res.data.success);
              props.history.goBack();
            }
          });
      }
    })
  }
};

export const copy = (record, props) => {
  return dispatch => {
    Modal.confirm({
      title: "提示",
      content: "确定复制这条素材吗？",
      onOk: () => {
        axios.post("/msg/create", {...record})
          .then(res => {
            if(res.data.success) {
              let data = res.data.data;
              message.success("复制成功");
              const action = {
                type: constants.COPY,
                data
              };
              dispatch(action);
            }
          });
      }
    })
  }
};

export const initReplyInfo = record => ({
  type: constants.INIT_REPLY_INFO,
  record
});

