import React, { Component } from 'react';
import { Table, Button, Tooltip } from 'antd';
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import backTop from '../../../utils';

import {actionCreators} from "./store";

class Reply extends Component {
  render() {
    const { replyList, deleteReply, copy } = this.props;
    const replyHeader =  [
      {
        title: "msgId",
        key: "msgId",
        dataIndex: "msgId",
        align: "center"
      },
      {
        title: "类型",
        key: "type",
        dataIndex: "type",
        align: "center",
        render: (text, record) => (record.type === 0 ? "文本消息"
          : "图文消息")
      },
      {
        key: "title",
        dataIndex: "title",
        title: "标题",
        align: "center",
        width: 100,
        render: (text, record) => {
          let title = record.contents ? record.contents[0].title : "";
          return (
            <Tooltip placement="top" title={title}><span className="textEllipsis" style={{width: 100}}>{title}</span></Tooltip>
          )
        }
      },
      {
        key: "description",
        dataIndex: "description",
        title: "详细信息",
        align: "center",
        width: 200,
        render: (text, record) => {
          let content = record.contents ? record.contents[0].description : "";
          return (
            <Tooltip placement="top" title={content}><span className="textEllipsis" style={{width: 200}}>{content}</span></Tooltip>
          )
        }
      },
      {
        key: "url",
        dataIndex: "url",
        title: "链接",
        align: "center",
        width: 100,
        render: (text, record) => {
          let url = record.contents ? record.contents[0].url : "";
          return (
            <Tooltip placement="top" title={url}><span className="textEllipsis" style={{width: 100}}>{url}</span></Tooltip>
          )
        }
      },
      {
        key: "picurl",
        dataIndex: "picurl",
        title: "图片",
        align: "center",
        render: (text, record) => (record.contents ? <img style={{width: 80, verticalAlign: "middle"}} src={record.contents[0].picurl} alt="图片找不到啦~"/> : <span></span>)
      },
      {
        title: "Action",
        key: "Action",
        align: "center",
        width: 200,
        render: (text, record, index) => (
          <div>
            <Button size="small" onClick={() => copy(record, this.props)}>复制</Button>
            <Button size="small" type="primary" style={{margin: "0 10px"}}>
              <Link to={{pathname: "/admin/source/reply/update", query: record}}>编辑</Link>
            </Button>
            <Button size="small" type="danger" onClick={() => deleteReply(record._id, index)}>删除</Button>
          </div>
        )
      }
    ];
    return (
    	<div>
    		<div className="container">
          <Button type="primary" style={{marginBottom: "20px"}}>
            <Link to="/admin/source/reply/create">添加新素材</Link>
          </Button>
          <Table size="small" rowKey={record => record._id} columns={replyHeader} dataSource={replyList} pagination={{onChange: () => backTop()}} />
        </div>
    	</div>
    )
  }
  componentDidMount() {
    this.props.getReplyList()
  }
}

const mapState = state => ({
  replyList: state.source.group.replyList
});

const mapDispatch = dispatch => ({
  getReplyList() {
    dispatch(actionCreators.getReplyList())
  },
  deleteReply(id, index) {
    dispatch(actionCreators.deleteReply(id, index))
  },
  copy(record, props) {
    dispatch(actionCreators.copy(record, props))
  }
});

export default connect(mapState, mapDispatch)(Reply);